import React from "react"
import { graphql } from "gatsby"
import {Trans, useTranslation} from "gatsby-plugin-react-i18next"
import Layout from "../components/Layout"
import Address from "../assets/svg/address.svg"
import Phone from "../assets/svg/phone.svg"
import Email from "../assets/svg/email.svg"

const Contact = ({data}) =>{

  const contactInfo = data.site.siteMetadata.contact

  const {t} = useTranslation(['header','contact'])

  return (
    <Layout title={t("header:CONTACT","CONTACT")}>
      <main id="contact">
        <div className="vertical-center">
          <h1 className="page-title"><Trans i18nKey="contact:contact">Contact Me</Trans></h1>
          <div className="container">
            <ContactAside contactInfo={contactInfo} />
            <ContactSection t={t}/>
          </div>
        </div>
      </main>
    </Layout>
  )
} 

const ContactAside = ({ contactInfo }) => (
  <aside className="contact-aside">
    <div>
      <h2><Trans i18nKey="contact:title">{contactInfo.title}</Trans></h2>
      <p>
        <em><Trans i18nKey="contact:subtitle">{contactInfo.subtitle}</Trans></em>
      </p>
    </div>
    <div>
      {contactInfo.info.map(elem => (
        <div key={elem.title} className="info">
          <div>
            {verifyIcon(elem.iconName)}
            <em className="info-title"><Trans i18nKey={`contact:${elem.title}`}>{elem.title}</Trans></em>
          </div>
          <p className="info-desc">{elem.desc}</p>
        </div>
      ))}
    </div>
  </aside>
)

const ContactSection = ({t}) => (
  <section className="contact-section">
    <form
      className="form"
      name="contact"
      method="post"
      action="https://getform.io/f/b5965691-22b2-431a-9634-ad26587abae2"
    >
      <h3 className="form-title"><Trans i18nKey="contact:form-title">Fill out the form</Trans></h3>
      <div>
        <input
          className="form-contact"
          type="text"
          name="name"
          placeholder={t("contact:name","Your name")}
          required
        />
      </div>
      <div>
        <input
          className="form-contact"
          type="email"
          name="email"
          placeholder={t("contact:email","Your email")}
          required
        />
      </div>
      <div>
        <input
          className="form-contact"
          type="text"
          name="subject"
          placeholder={t("contact:subject","Subject")}
          required
        />
      </div>
      <div>
        <textarea
          className="form-contact textarea"
          name="message"
          placeholder={t("contact:message","Message")}
          required
        ></textarea>
      </div>
      <div>
        <button className="form-contact button" type="submit">
          <Trans i18nKey="contact:send">SEND</Trans>
        </button>
      </div>
    </form>
  </section>
)

const verifyIcon = iconName => {
  switch (iconName) {
    case "Address":
      return <Address />
    case "Phone":
      return <Phone />
    case "Email":
      return <Email />
    default:
      console.log(iconName)
  }
}


export const contactQuery = graphql`
  query {
    site {
      siteMetadata {
        contact {
          title
          subtitle
          info {
            iconName
            title
            desc
          }
        }
      }
    }
  }
`

export default Contact
